@import url(https://fonts.googleapis.com/css2?family=Nerko+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nerko+One&family=Open+Sans+Condensed:wght@300&family=Playfair+Display:ital@0;1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nerko+One&family=Open+Sans+Condensed:wght@300&family=PT+Sans:ital,wght@1,700&family=Playfair+Display:ital@0;1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box}[className*="col-"]{float:left;padding:15px}[className*="col-"]{width:100%}@media only screen and (min-width: 768px){.col-1{width:8.33%}.col-2{width:16.66%}.col-3{width:25%}.col-4{width:33.33%}.col-5{width:41.66%}.col-6{width:50%}.col-7{width:58.33%}.col-8{width:66.66%}.col-9{width:75%}.col-10{width:83.33%}.col-11{width:91.66%}.col-12{width:100%}}.row::after{content:"";clear:both;display:table}.App{background-color:black;color:white;display:flex;text-align:center;justify-content:center;flex-direction:column}.App #header{display:block;padding:30px;font-family:'Nerko One', cursive;font-size:50px}.App #drum-machine{display:flex;justify-content:space-around;background:black;margin:50px 40px 40px 50px}.App #drum-machine .display{display:flex;flex-direction:column;text-align:center;justify-content:center;margin:auto;font-family:'Roboto Condensed', sans-serif;width:50%}.App #drum-machine .display .changeSound{margin-bottom:20px}.App #drum-machine .display .changeSound .switch{background-color:purple;display:flex;border-radius:20px}.App #drum-machine .display .changeSound .switch .switcher{width:100%;text-align:center;border-radius:20px;border:none;background-color:purple;padding:20px;font-size:20px;color:white}.App #drum-machine .display .changeSound .chord{padding:20px;background-color:black;color:white;border-radius:20px}.App #drum-machine .display #volume{display:flex;margin:auto}.App #drum-machine .display #volume .mute{border-radius:20px;border:none;padding:10px;margin-left:10px}.App #drum-machine .pads{display:grid;grid-template-columns:auto auto auto;width:50%}.App #drum-machine .pads .drum-pad{font-size:20px;padding:5px;border-radius:10px;background-color:#4d0080;color:white;font-weight:900}

